*{
	margin:0;
	font-family: 'Poppins', sans-serif;
	color: #444;
}

nav{
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0 0 3% 0;
	box-shadow :13px 13px 30px #00000025;

}

nav a{
	text-decoration: none;
	text-transform: uppercase;
	margin: 1% 2%;
}

.current {
	text-decoration: none;
}

h1, h2 {
	font-family: 'Dela Gothic One', sans-serif;
}

footer {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	padding: 2% 0;

}

footer h3{
	text-transform: uppercase;
	font-weight: 600;
	color:white;
}

footer p {
	color:white;

}

footer a{
	text-decoration: none;
	color:white;

}

.footer-part{
	display: flex;
	flex-direction: column;
	align-items: center;
}


.social-networks{
	display: flex;
	flex-direction: row;
	justify-content:space-between;
	align-items: center;

}

.picto{
	color:white;

}